@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --body-midd: Inter;

  /* font sizes */
  --body-midd-size: 16px;
  --h3-size: 38px;
  --h4-size: 24px;
  --h2-size: 54px;
  --body-small-size: 14px;

  /* Colors */
  --neutral-1: #07090a;
  --primary-4: #dccfff;
  --color-floralwhite: #fffaef;
  --neutral-light-4: #fff;
  --gray-500: #2e3646;
  --primary-1: #6a35f5;
  --color-black: #000;
  --color-midnightblue: #120040;
  --neutral-light-3: #f7f7fc;

  /* Gaps */
  --gap-5xl: 24px;
  --gap-37xl: 56px;
  --gap-13xl: 32px;
  --gap-xl: 20px;
  --gap-5xs: 8px;
  --gap-96xl: 115px;
  --gap-base: 16px;
  --gap-3xs: 10px;
  --gap-21xl: 40px;
  --gap-27xl: 46px;
  --gap-225xl: 244px;

  /* Paddings */
  --padding-34xl-5: 53.5px;
  --padding-5xl: 24px;
  --padding-45xl: 64px;
  --padding-base: 16px;
  --padding-21xl: 40px;
  --padding-38xl: 57px;
  --padding-34xl: 53px;
  --padding-65xl-4: 84.4px;

  /* Border radiuses */
  --br-xl: 20px;
  --br-3xs: 10px;
  --br-184xl: 203px;
  --br-206xl: 225px;
}
